import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './App.vue';
import vuetify from './plugins/vuetify';
import UserSettings from './plugins/UserSettings';
import Form from './components/Form';
import Settings from './components/Settings';

Vue.config.productionTip = false;

const routes = [
  { 
    path: '/form', 
    component: Form 
  },
  { 
    path: '/settings', 
    component: Settings
  },
  {
    path: '/',
    redirect: '/settings'
  }
];
const router = new VueRouter({
  routes // short for `routes: routes`
});

Vue.use(VueRouter);

new Vue({
  vuetify,
  router,
  render: h => h(App),
  beforeMount: function() {
    // Load text and card colours from a user settings service
    let that = this;
    const promise = new Promise((resolve) => {
      UserSettings().then(function(data) {
        that.$vuetify.theme.themes.light.card = data.light.card;
        that.$vuetify.theme.themes.light.text = data.light.text;
        that.$vuetify.theme.themes.dark.card = data.dark.card;
        that.$vuetify.theme.themes.dark.text = data.dark.text;
        resolve();
      });
    });
    return promise;
  }
}).$mount('#app')
